@use '../../assets/styles/globals' as *;

.dashboard-options {
  height: 50px;
  border: 0;
  background: $secondary-color;
  color: #fff;
  font-size: 16px;
  padding: 2px 20px;
  box-sizing: border-box;
  position: absolute;
  left: 40%;
  top: 0%;
  display: flex;
  z-index: 2;

  .dashboard-options-button {
    flex: 1;
    border: 0;
    background: transparent;
    color: #fff;
    font-size: 16px;
    padding: 2px 20px;
    box-sizing: border-box;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);

    &:hover {
      background: $primary-color;
    }

    &.active {
      background: $primary-color;
    }
  }
}

.dashboard-page {
  padding-left: 100px;
  padding-right: 50px;
  width: calc(100% - 150px);
  position: initial;
  height: 100%;
  overflow: auto;

  h1.page-title {
    margin-left: 100px;
    margin-top: 100px;
  }

  .form-container {
    width: 49%;
    margin-left: 2%;
    float: left;
    clear: none;
  }

  input[type='text'],
  input[type='email'] {
    width: 100%;
    border: 0;
    background: $secondary-color;
    height: 50px;
    font-size: 16px;
    color: #fff;
    padding: 0 20px;
    box-sizing: border-box;
  }

  textarea {
    width: 100%;
    border: 0;
    background: $secondary-color;
    height: 50%;
    font-size: 16px;
    color: #fff;
    padding: 20px;
    min-height: 150px;
    box-sizing: border-box;
  }

  .fileInput {
    cursor: pointer;
    width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
    color: #fff;
    font-size: 16px;
    margin-bottom: 20px;
  }

  .images-container {
    display: flex;
    gap: 15px;
    flex-wrap: wrap;
    padding-bottom: 100px;
  }

  .previewText {
    text-align: center;
    width: 100%;
    margin-top: 20px;
  }

  .flat-button {
    color: $primary-color;
    background: 0 0;
    font: 11px;
    letter-spacing: 3px;
    text-decoration: none;
    padding: 8px 10px;
    border: 1px solid $primary-color;
    border-radius: 4px;
    margin-right: 10px;

    &:hover {
      background: $primary-color;
      color: #333;
    }
  }

  .image-box {
    position: relative;
    flex: 1 1 20%;
    height: 400px;
    overflow: hidden;
    border-radius: 10px;
    max-width: calc(25% - 10px);

    .edit-btn {
      position: absolute;
      top: 10px;
      right: 10px;
      z-index: 4;
      padding: 0 23px;
      height: 40px;
      line-height: 34px;
      border: 2px solid $primary-color;
      border-radius: 4px;
      font-size: 1rem;
      color: #fff;
      background: transparent;
      text-transform: uppercase;
      font-weight: 700;
      transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
      cursor: pointer;
    }

    .edit-btn:hover {
      transform: translateY(-3px);
      background: $primary-color;
      color: #fff;
    }

    .portfolio-image {
      position: absolute;
      z-index: 2;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .content {
      position: absolute;
      width: 100%;
      z-index: 3;
      padding: 10px 20px;
      transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
      background: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.2) 0%,
        rgba(0, 0, 0, 1) 100%
      );
      bottom: -70px;
    }

    .title {
      margin-bottom: 0;
      margin-top: 0;
      color: #fff;
      font-size: 1.5rem;
      font-weight: 500;
      line-height: 1.5rem;
      width: 75%;
    }

    .description {
      font-size: 1rem;
      margin-bottom: 5px;
      color: #fff;
      font-weight: 700;
      width: 75%;
    }

    .btn {
      margin-top: 30px;
      margin-bottom: 10px;
      padding: 0 23px;
      height: 40px;
      line-height: 34px;
      border: 2px solid $primary-color;
      border-radius: 4px;
      font-size: 1rem;
      color: #fff;
      background: transparent;
      text-transform: uppercase;
      font-weight: 700;
      transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
      cursor: pointer;
    }

    .btn:hover {
      transform: translateY(-3px);
      background: $primary-color;
      color: #fff;
    }

    &:after {
      content: '';
      background: linear-gradient(180deg, $primary-color, #000);
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
      transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
      opacity: 0;
    }

    &:hover:after {
      opacity: 0.85;
    }

    &:hover .content {
      bottom: 0;
      background: transparent;
    }
  }

  .no-preview {
    height: 400px;
    overflow: hidden;
    border-radius: 10px;
    max-width: calc(25% - 10px);
    justify-content: center;
    align-items: center;
    background: $secondary-color;
    color: #fff;
    font-size: 1.5rem;
    font-weight: 700;
    padding: 15px;
  }
}

.popup-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;

  .popup {
    position: relative;
    width: 50%;
    height: 50%;
    background: $secondary-color;
    border-radius: 10px;
    padding: 20px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: #fff;

    .popup-text {
      font-size: 1.5rem;
      font-weight: 700;
      margin-bottom: 20px;
    }

    .flat-button:hover {
      background: $primary-color;
      color: #333;
    }
  }
}
